export default {
	data() {
		return {
			username: '',
			password: '',
			department: '',
			pwdType: 'password',
			btnDisabled: false,
			switchImg: '/static/image/icon_pwd_hide.png',
			btnColor: '#91C5F7'
		}
	},
	methods: {
		queryUser(e) {
			if (this.username != '') {
				// 查询用户所在店铺
				this.department = "总仓";
			}
		},
		checkUser(e) {
			this.username = e.target.value;
			this.department = '';
			if (this.username != '' && this.password != '') {
				this.btnColor = '#2886E8';
				this.btnDisabled = true;
			} else {
				this.btnColor = '#91C5F7';
				this.btnDisabled = false;
			}
		},
		checkPwd(e) {
			this.password = e.target.value;
			if (this.username != '' && this.password != '') {
				this.btnColor = '#2886E8';
				this.btnDisabled = true;
			} else {
				this.btnColor = '#91C5F7';
				this.btnDisabled = false;
			}
		},
		delUser(e) {
			this.username = '';
			this.password = '';
			this.department = '';
			this.btnColor = '#91C5F7';
			this.btnDisabled = false;
		},
		switchPwd(e) {
			if (this.pwdType == 'text') {
				this.pwdType = 'password';
				this.switchImg = '/static/image/icon_pwd_hide.png'
			} else {
				this.pwdType = 'text';
				this.switchImg = '/static/image/icon_pwd_show.png'
			}
		},
		login() { // 登录成功
			if (this.btnDisabled) {
				this.$API.tab('../tab/main/main');
			}
		}
	}
}
